form {
    width: 90%;
    max-width: 300px;
    background-color: white;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family:montserrat,Arial,Helvetica,sans-serif;
    border-radius: 10px;
  }
  
  textarea {
    background-color: #F9FBFC;
    border: 2px solid #49abfc;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  textarea::placeholder {
    color: gray;
    font-family:'Courier New', Courier, monospace;
    font-weight: 400;
    font-family:montserrat,Arial,Helvetica,sans-serif;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: white;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 10px;
    border: none;
    font-family:montserrat,Arial,Helvetica,sans-serif;
  }
  
  .TodoForm-button--add {
    background: #49abfc;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  }
  
  .TodoForm-button--cancel {
    background: rgb(255, 94, 102);
  }